import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Home from '../../Pages/Home/Home';
import AppBar from '../../Pages/AppBar/AppBar';
import data from '../../Data/data.json';
import BrushIcon from '@material-ui/icons/Brush';

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundColor: '#cfe8fc', height: 'auto',
    backgroundImage: 'linear-gradient( 150deg, #e7b4de 30%, #86a1e2 70%, #a0cffa 100%)',
    borderRadius: '5px',
    boxShadow: '-3px -2px gray',
    margin: '1%',
    flexGrow: 1,
    minWidth: '1215px',
    minHeight: '720px',
  },
  header:{
    padding: theme.spacing(3),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  headerAd: {
    padding: theme.spacing(0),
    textAlign: 'center',
    color: theme.palette.text.primary,
  },
  lefttabs: {
    padding: theme.spacing(2),
    textAlign: 'left',
    color: theme.palette.text.primary,
    minHeight: '350px',
    position: '-webkit-sticky',
    position: 'sticky',
    top: '5px',
    declaration: 'none',
  },
  tabs: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    minHeight: '550px',
  },
  logo: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    borderRadius:'50px',
  },
  paper: {
    padding: theme.spacing(4),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },

}))
function DuiLayout({tabs,selection}) {
  const classes = useStyles();
  const theme = useTheme();
  return (<React.Fragment>
    <CssBaseline />
    <Container maxWidth="xl" >
      <Typography component="div" className={classes.container}>
        <Grid
          container
          direction="row"
          justify="center"
          alignItems="stretch"
          spacing={1}>
          <Grid item xs={12} sm={11}>
          <AppBar tabs={tabs} selection={selection}></AppBar>
          </Grid>
          <Grid item xs={12} sm={11}>
            <Paper className={classes.headerAd}>
              <marquee behavior="alternate" scrollamount="3">
                <dl >
              <dt style={{display: 'inline-block'}}><p>* * * * * * * * *</p></dt>
              <dt style={{display: 'inline-block'}}><a href="https://www.youtube.com/channel/UCYCHGSUh5CjlV8SAqZfRyyQ" target="_blank">   Youtube chennal link</a></dt>
              <dt style={{display: 'inline-block'}}><p>* * * * * * * * *</p></dt>
              <dt style={{display: 'inline-block'}}><a href="https://www.youtube.com/watch?v=6CTiI9emNSM" target="_blank"> Latest Training Updated</a></dt>
              <dt style={{display: 'inline-block'}}><p>* * * * * * * * *</p></dt>
              </dl>
              </marquee>
            </Paper>
          </Grid>
          <Grid item xs={12} sm={2}>
            <Paper className={classes.lefttabs}>
              {data[selection[0]].Indexs?
              <>
              <p><strong>Index of <a href={"#"+selection[0]} style={{textDecoration:'none'}}>{selection[0]}<BrushIcon/></a></strong></p>
              <dl>
            {data[selection[0]].Indexs.map((index,key)=>{
              return<dt><a href={"#"+index} style={{textDecoration:'none',fontSize:'12px'}}>{index}<BrushIcon/></a></dt>
            })}
            </dl>
            </>
            :<></>}
            </Paper>
          </Grid>
          <Grid item xs={12} sm={9}>
            <Paper className={classes.tabs}>{tabs[selection[0]]}</Paper>
          </Grid>
          <Grid item xs={12} sm={11}>
            <Paper className={classes.paper}>Footer</Paper>
          </Grid>
        </Grid>
      </Typography>
    </Container>
  </React.Fragment>);
}

export default DuiLayout;