import React from 'react';
import './Home.css';
import data from '../../Data/data.json';

function importAll(r) {
    return r.keys().map(r);
  }
  

class Home extends React.Component {
    render(){
    return (
        <>
        <div className='home' id="Home">
            <p className='welcome'>
                <h1>{data.Home.Title[0]}</h1>
                <h3>{data.Home.Title[1]}</h3>
                <h4>{data.Home.Title[2]}</h4>
                <img src={process.env.PUBLIC_URL+data.Home.image} className="balaji"/>
            </p>
            {data.Home.Topics.map((topic,key)=>{
                return (
                    <>
                    <h3 id={topic.Title}>{topic.Title}</h3>
                    {topic.Content.map((text,k)=>{
                       return <p>{text}</p>
                    })}
                    <ol>
                     {topic.Items?topic.Items.map((item,i)=>{
                         return <li>{item}</li>
                     }):()=>{}}
                    </ol>
                    </>
                )
            })}
        </div>
        </>
    )
}
}

export default Home;