import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import ListSubheader from '@material-ui/core/ListSubheader';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';
import tileData from '../../Data/data.json';
import Card from '@material-ui/core/Card';
import './play.css';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: '80%',
    height: 'auto',
  },
  icon: {
    color: 'rgba(255, 255, 255, 0.54)',
  },
  card: {
    maxWidth: 300,
    backgroundColor: '#000',
    boxShadow:'-2px -6px gray',
    cursor: 'pointer',
  },
}));

/**
 * The example data is structured as follows:
 *
 * import image from 'path/to/image.jpg';
 * [etc...]
 *
 * const tileData = [
 *   {
 *     img: image,
 *     title: 'Image',
 *     author: 'author',
 *   },
 *   {
 *     [etc...]
 *   },
 * ];
 */

const PlayCard = ({close,url})=>{
  return (
    <div className='play' onClick={()=>{close(false)}}>
      <button className='close-btn' onClick={()=>{close(false)}}>X</button>
    <Card className='content'>
      <iframe width="98%" height="98%" src={url} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
    </Card>
    </div>
  )
}
export default function Learn() {
  const classes = useStyles();
  const learnData = tileData.Learn;
  const [play,setplay] = useState(false);
  const [url,setUrl] = useState('');
  const playHandel = (url)=>{
    setUrl(url);
    setplay(true)
  }

  return (
    <>
    {play?<PlayCard close={setplay} url={url}></PlayCard>:<></>}
    <div className={classes.root}>
      <GridList  className={classes.gridList} space>
        <GridListTile key="Subheader" cols={2} style={{ height: 'auto' }}>
          <ListSubheader component="div">Balaji</ListSubheader>
        </GridListTile>
        {Object.keys(learnData).map((key,index) => {
            return <GridListTile key={key}>
           <Card className={classes.card} onClick={()=>playHandel(learnData[key].URL)}> <img src={process.env.PUBLIC_URL+"/images/Learn/"+learnData[key].image} alt={learnData[key].videoName} width='280' height='180'/>
            <GridListTileBar
            titlePosition='bottom'
            style={{backgroundColor:'rgba(0, 0, 0, 0.3)'}}
              title={<span style={{fontWeight:'bold',color:'#fff'}}>{learnData[key].videoName}</span>}
            />
            </Card>
          </GridListTile>
            })}
      </GridList>
    </div>
    </>
  );
}
