import React, { useState } from 'react';
import MuiLayout from './Layout/MuiLayout'
import AboutUs from '../Pages/AboutUs/Aboutus';
import Contact from '../Pages/Contact/Contact';
import Home from '../Pages/Home/Home';
import Learn from '../Pages/Learn/Learn';

import PermMediaIcon from '@material-ui/icons/PermMedia';
import HomeIcon from '@material-ui/icons/Home';
const tabs ={
    'Home' : <Home/>,
    'Gallery' : <div> My Art Collection </div>,
    'shop' : <div> Make it yours... just buy it.</div>,
    'Learn' : <div> training videos <Learn/></div>,
    'AboutUs' : <AboutUs/>,
    'Contact' : <Contact/>,
  }

  const icons ={
    'Home' : <HomeIcon/>,
    'Gallery' : <PermMediaIcon/>,
    'shop' : <div> Make it yours... just buy it.</div>,
    'Learn' : <div> training videos </div>,
    'AboutUs' : <AboutUs/>,
    'Contact' : <Contact/>,
  }
function MuiApp (){
    const selection = useState('Home');
    return (<> 
        <MuiLayout tabs={tabs} tab={selection}/>
    </>);
}

export default MuiApp;